import React from 'react';

import {createStackNavigator} from 'react-navigation-stack';
import {Icon} from 'react-native-elements';

import SearchHome from '../views/SearchHome';
import InputDetails from '../views/input_details';

import config from '../config/stack';
import {Button, Image, TouchableHighlight, View, Text} from "react-native";
import {TouchableOpacity} from "react-native-gesture-handler";

const InputTabView = ({navigation}) => <SearchHome navigation={navigation}/>;

const InputDetailTabView = ({navigation}) => (
    <InputDetails
        banner={`${navigation.state.params.name}s Profile`}
        navigation={navigation}
    />
);

const SearchTab = createStackNavigator({
    Input: {
        screen: InputTabView,
        path: '/',
        navigationOptions: ({navigation}) => ({
            title: navigation.getParam('status') ? '汉服点评(测试)' : '汉服点评(测试)',
            headerLeft: (
                <TouchableHighlight onPress={() => {
                    navigation.toggleDrawer();
                }}>
                    <Image style={{
                        alignItems: 'center',
                        justifyContent: 'center',
                        width: 36,
                        height: 36,
                        marginLeft: 16,
                        borderColor: 'green',
                        borderRadius: 13,
                    }}
                           source={{uri: navigation.getParam('avatar') ? navigation.getParam('avatar') : 'https://hanbei-1256982553.cos.ap-chengdu.myqcloud.com/common_icon/icon_unlogin.png'}}
                           onPress={() => navigation.toggleDrawer()}>
                    </Image>
                </TouchableHighlight>


            ),
            headerRight: (<View >
                <Text style={{marginHorizontal:10}}>{navigation.getParam('name') ? navigation.getParam('name') : '未登录'}</Text>
            </View>)
        }),
    },
    Input_Detail: {
        screen: InputDetailTabView,
        path: '/input_detail',
        navigationOptions: {
            title: 'Input Detail',
        },
    },
}, config);


export default SearchTab;